.formulario {
  display: flex;
  justify-content: space-evenly;
  margin: 80px 50px;
  flex-wrap: wrap;
  gap: 40px;
}

.formulario form {
  background-color: #f6f6f6;
  border-radius: 20px;
  box-shadow: 7px 7px 15px rgba(0, 0, 0, 25%);
  padding: 8px 70px;
  flex: 1;
}

.formulario h2 {
  font-size: 32px;
  font-weight: 400;
  color: 212121;
}