.colaborador {
  width: 280px;
  margin-bottom: 24px;
  position: relative;
}

.colaborador .encabezado {
  background-color: #F0F0F0;
  border-radius: 10px 10px 0px 0px;
}

.colaborador img {
  width: 150px;
  border-radius: 50%;
  position: relative;
  bottom: -50%;
}

.colaborador .info {
  background: #FFFFFF;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
  border-radius: 0px 0px 10px 10px;
  padding-top: 40px;
  padding-bottom: 40px;
  min-height: 100px;
  font-family: 'Montserrat', sans-serif;
}

.colaborador .info .h4 {
  color: #6378F7;
  font-size: 18px;
  line-height: 22px;
  font-weight: bold;
  margin-top: 8px;
}

.colaborador .info h5 {
  font-size: 18px;
  line-height: 22px;
  color: #212121;
  padding: 0 24px;
  font-weight: 400;
  margin: 0;
}

.eliminar {
  position: absolute;
  right: -15px;
  top: -15px;
}