.lista-opciones label {
  display: block;
  font-size: 18px;
  font-weight: 200;
  font-family: "Monserrat", sans-serif;
  margin-bottom: 8px;
}

.lista-opciones select {
  width: 100%;
  background-color: ffffff;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
  padding: 24px;
  border: none;
  outline-color: #6278F7;
  font-size: 16px;
  font-family: "Monserrat", sans-serif;
}