.campo {
  font-family: 'Montserrat', sans-serif;
  margin: 24px 0;
}

.campo label {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  font-weight: 600;
  display: block;
  margin-bottom: 8px;
}

.campo input {
  width: 100%;
  border: none;
  background-color: #FFFFFF;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
  padding: 30px 20px;
  border-radius: 10px;
  outline-color: #6278f7;
  font-size: 16px;
  box-sizing: border-box;
}

.campo-color input {
  height: 40px;
  padding: 5px 20px;
}